import React, {ChangeEvent, useState} from 'react'
import Greeting from './Greeting'
import {UserType} from './HW3';

type GreetingContainerPropsType = {
    users: UserType[]; // need to fix any do
    addUserCallback: (n: string) => void; // need to fix any do
}

// более простой и понятный для новичков
// function GreetingContainer(props: GreetingPropsType) {

// более современный и удобный для про :)
// уровень локальной логики
const GreetingContainer:
    React.FC<GreetingContainerPropsType> = ({users, addUserCallback}) => { // деструктуризация пропсов
    const [name, setName] = useState<string>('') // need to fix any do
    const [error, setError] = useState<string>('') // need to fix do
    const nameTrim = name.trim();
    const setNameCallback = (e: ChangeEvent<HTMLInputElement>) => { // need to fix any do
        setName(e.currentTarget.value); // need to fix do
        setError('');
    }
    const addUser = () => {
        nameTrim && addUserCallback(nameTrim);
        setName('');
        !nameTrim && setError('You must enter name');
        nameTrim&&alert(`Hello,${name}!`) // need to fix do
    }

    const totalUsers = users.length// need to fix do

    return (
        <Greeting
            name={name}
            setNameCallback={setNameCallback}
            addUser={addUser}
            error={error}
            totalUsers={totalUsers}
        />
    )
}

export default GreetingContainer
